










































import {Component, Vue} from 'vue-property-decorator'
import {DeleteTableData, getTableData} from "@/api/api";

@Component
export default class Table extends Vue {
    public $echarts: any;
    //表格数据
    tableData: any = []
    tableLoading: boolean = false

    // 分页数据
    pageSize: number = 10
    pageNum: number = 1
    total: number = 0

    mounted() {
        this.fn1()
        this.initTable()
    }

    //echarts图
    fn1(): void {
        var myChart = this.$echarts.init(document.getElementById('echarts1'));
        window.addEventListener('resize', function () {
            myChart.resize();
        });
        myChart.setOption({
            //tooTip组件
            tooltip: {
                show: true,
                trigger: "axis"
            },
            //弹窗工具
            toolbox: {
                show: true,
                feature: {
                    dataView: {
                        show: true
                    },
                    saveAsImage: {
                        title: '保存为图片',
                        show: true
                    },
                    restore: {
                        show: true
                    },
                    dataZoom: {
                        show: true
                    },
                    magicType: {
                        type: ['line', 'bar']
                    }
                }
            },
            xAxis: {
                data: ["一月", "二月", "三月", "四月", "五月", "六月"]
            },
            yAxis: [{
                type: 'value',
                name: '水量',
                min: 0,
                max: 250,
                interval: 50,
                axisLabel: {
                    formatter: '{value} ml'
                }
            },
                {
                    type: 'value',
                    name: '温度',
                    min: 0,
                    max: 25,
                    interval: 5,
                    axisLabel: {
                        formatter: '{value} C'
                    }
                }],
            series: [
                {
                    name: "蒸发量",
                    type: 'bar',
                    data: [72.0, 123.0, 293.5, 193.5, 234.5, 223.0, 122.0, 167.0]
                },
                {
                    name: "降水量",
                    type: 'bar',
                    data: [142.0, 174.0, 293.5, 143.2, 342.5, 263.0, 222.0, 167.0]
                },
                {
                    name: "平均温度",
                    type: 'line',
                    // yAxisIndex: 1,
                    data: [23.0, 132.0, 36.5, 476.4, 334.5, 223.0, 122.0]
                }
            ]
        });
    }

    // 获取 table 数据
    initTable(): void {
        console.log("我进去了。。。")
        this.tableLoading = true

        getTableData({
            pageNum: this.pageNum
        }).then((res: any) => {
            this.tableLoading = false
            if (res.code === 200) {
                this.tableData = res.data.list
                this.total = res.data.total
            } else {
                this.$message.error(res.msg)
            }
        })
    }

    // 删除数据
    deleteData(id: number): void {
        this.$confirm('确认删除这条记录么？', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
            DeleteTableData(id).then((res: any) => {
                if (res.code === 200) {
                    this.$message.success('删除成功')
                    this.initTable()
                } else {
                    this.$message.error(res.msg)
                }
            })
        }).catch(() => {
        })
    }

    //分页显示
    handleCurrentChange(val: number): void {
        this.pageNum = val
        this.initTable()
    }
}
